import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/service/common.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.scss']
})
export class PageComponent implements OnInit {

  constructor(public service:CommonService, private router:Router) { }

  content;
 
  curr_page = this.service.curr_page;
  total_page = 3;
  audio_icon = "audio.png";
  page;
  
  ngOnInit() {
    this.content = this.service.content;
    this.page = this.service.getPageContent();
    this.addPage(0);
  }

  addPage(i){
    
    var newPage = {
      "title": "New Page",
      "row": []
    };
    this.service.content.menu.topic.push(newPage);
    this.gotoPage(i+1);
    console.log('-', i);
  }

  gotoPage(i){
    this.service.setPageNo(i);
    this.page = this.service.getPageContent();
    console.log(this.page);
    //this.router.navigateByUrl('course/1/page/'+i);
  }
}
